import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
import faker from 'faker';

// material
import { Save as SaveIcon } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
import {
  Autocomplete,
  Box,
  Card,
  Chip,
  Grid,
  Stack,
  Switch,
  TextField,
  Typography,
  FormHelperText,
  FormControlLabel,
  FormLabel,
  RadioGroup,
  Radio,
} from '@mui/material';
// utils
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// components
import { UploadSingleFile } from '../upload';
import { QuillEditor } from '../editor';

// ----------------------------------------------------------------------

const userList = [...Array(10)].map(() => faker.internet.userName());

const NewNotificationSchema = Yup.object().shape({
  name: Yup.string().required('Name is required'),
  notificationId: Yup.string(),
  notificationType: Yup.string(),
  image: Yup.mixed(),
  isManual: Yup.boolean(),
  message: Yup.string(),
  users: Yup.array().of(Yup.string()),
});

NotificationNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentNotification: PropTypes.object,
};

export default function NotificationNewForm({ isEdit, currentNotification }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: currentNotification?.name || '',
      notificationId: currentNotification?.notificationId || '',
      notificationType: currentNotification?.notificationType || '',
      image: currentNotification?.image || null,
      isManual: currentNotification?.isManual || true,
      message: currentNotification?.message || '',
      users: currentNotification?.users || [],
    },
    validationSchema: NewNotificationSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.notification.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  console.log('*** ', values, errors);

  const handleImageDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('image', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    size="small"
                    label="Name"
                    {...getFieldProps('name')}
                    error={Boolean(touched.name && errors.name)}
                    helperText={touched.name && errors.name}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    size="small"
                    label="Notification ID"
                    placeholder="Notification ID"
                    {...getFieldProps('notificationId')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.notificationId && errors.notificationId)}
                    helperText={touched.notificationId && errors.notificationId}
                  >
                    <option value="" />
                    {[
                      { label: 'ID 1', value: 'id-1' },
                      { label: 'ID 2', value: 'id-2' },
                    ].map((option) => (
                      <option key={option.value} value={option.label}>
                        {option.label}
                      </option>
                    ))}
                  </TextField>
                </Stack>

                <Stack
                  direction={{ xs: 'column', sm: 'row' }}
                  spacing={{ xs: 3, sm: 2 }}
                  justifyContent="space-between"
                >
                  <FormLabel component="legend">Manual</FormLabel>
                  <FormControlLabel
                    labelPlacement="start"
                    control={<Switch {...getFieldProps('isManual')} checked={values.isManual} />}
                    label={
                      <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                        Yes
                      </Typography>
                    }
                    sx={{ mx: 0, width: 1, justifyContent: 'end' }}
                  />
                </Stack>
                <Stack
                  direction={{ xs: 'column', sm: 'row' }}
                  spacing={{ xs: 3, sm: 2 }}
                  justifyContent="space-between"
                >
                  <FormLabel component="legend">Notification Type</FormLabel>
                  <RadioGroup row aria-label="position" name="notificationType">
                    <FormControlLabel value="image" control={<Radio />} label="Image" labelPlacement="start" />
                    <FormControlLabel value="text" control={<Radio />} label="Text" labelPlacement="start" />
                  </RadioGroup>
                </Stack>

                <FormLabel component="legend">Message</FormLabel>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <div>
                    <QuillEditor
                      id="post-content"
                      placeholder="Write message here..."
                      value={values.message}
                      onChange={(val) => setFieldValue('message', val)}
                      error={Boolean(touched.message && errors.message)}
                    />
                    {touched.message && errors.message && (
                      <FormHelperText error sx={{ px: 2, textTransform: 'capitalize' }}>
                        {touched.message && errors.message}
                      </FormHelperText>
                    )}
                  </div>
                </Stack>

                <Card sx={{ py: 10, px: 3 }}>
                  <Box sx={{ mb: 5 }}>
                    <div>
                      <UploadSingleFile
                        maxSize={3145728}
                        accept="image/*"
                        file={values.image}
                        onDrop={handleImageDrop}
                        error={Boolean(touched.image && errors.image)}
                      />
                      {touched.image && errors.image && (
                        <FormHelperText error sx={{ px: 2 }}>
                          {touched.image && errors.image}
                        </FormHelperText>
                      )}
                    </div>
                  </Box>
                </Card>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  {values.isManual && (
                    <Autocomplete
                      multiple
                      fullWidth
                      {...getFieldProps('users')}
                      onChange={(event, newValue) => {
                        console.log('newValue: ', newValue);
                        setFieldValue('users', newValue);
                      }}
                      options={userList.map((option) => option)}
                      renderTags={(value, getTagProps) =>
                        value.map((option, index) => (
                          <Chip {...getTagProps({ index })} key={option + index} size="small" label={option} />
                        ))
                      }
                      renderInput={(params) => <TextField {...params} label="Users" />}
                    />
                  )}
                </Stack>
                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                    Save
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
