import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../routes/paths';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import NotificationNewForm from '../components/notification/NotificationNewForm';

// ----------------------------------------------------------------------

export default function NotificationCreate() {
  const { themeStretch } = useSettings();
  const { pathname } = useLocation();
  const { name } = useParams();
  const isEdit = pathname.includes('edit');

  // TODO: will be updated with brand
  const currentNotification = {};

  return (
    <Page title="Notification: Create a new notification">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Add Notification' : 'Edit Notification'}
          links={[{ name: 'Notification', href: PATH_DASHBOARD.root }, { name: !isEdit ? 'Add Notification' : name }]}
        />
        <NotificationNewForm isEdit={isEdit} currentNotification={currentNotification} />
      </Container>
    </Page>
  );
}
